import { Component, OnInit } from '@angular/core';
import { News, NewsList, ListNews, ListOrder } from './../../news/models/news';

import { Store } from '@ngrx/store';
import * as controlActions from '../store/control.actions';
import {Actions, ofType} from '@ngrx/effects';

@Component({
  selector: 'app-news-cg',
  templateUrl: './news-cg.component.html',
  styleUrls: ['./news-cg.component.css']
})
export class NewsCgComponent implements OnInit {

  NewsList = new NewsList();
  selectedNews: ListNews;
  selectedNewsKey = 0;

  viewingKeyTmp: number = null;
  viewingKey: number = null;

  constructor(
    private store: Store<any>,
    private updates$: Actions,
  ) { }

  ngOnInit() {
    this.store.dispatch(new controlActions.GetNewsCgNews());

    this.updates$
      .pipe(ofType(controlActions.GET_NEWS_CG_NEWS_SUCCESS))
      .subscribe(() => {
        this.store.select(store => store.ControlState.newsCgState)
          .subscribe((list: NewsList) => {
            this.NewsList = list;
            if(this.NewsList.list_news){
              this.selectedNews = this.NewsList.list_news[this.selectedNewsKey];
            }else{
              this.selectedNews = null;
            }
          });
      });

    this.updates$
      .pipe(ofType(controlActions.UPDATE_NEWS_CG_NEWS_SUCCESS))
      .subscribe(() => {
        this.viewingKey = this.viewingKeyTmp;
      });
  }

  nextNews() {
    if (this.selectedNewsKey < (this.NewsList.list_news.length - 1)){
      this.selectedNewsKey = this.selectedNewsKey + 1;
    }
    this.selectedNews = this.NewsList.list_news[this.selectedNewsKey];
  }

  previousNews() {
    if (this.selectedNewsKey > 0){
      this.selectedNewsKey = this.selectedNewsKey - 1;
    }
    this.selectedNews = this.NewsList.list_news[this.selectedNewsKey];
  }

  updateCg(id: number) {
    this.viewingKeyTmp = id;
    this.store.dispatch(new controlActions.UpdateNewsCgNews({ news_bar_id: id }));
  }

  stopCg() {
    this.store.dispatch(new controlActions.StopNewsCg());
  }

}
