import { Component, OnInit, Inject, Input, EventEmitter } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { slideData } from '../onair/onair.component';
import { Language } from '../../news/models/news';

@Component({
  selector: 'app-slide-data-dialog',
  templateUrl: './slide-data-dialog.component.html',
  styleUrls: ['./slide-data-dialog.component.css']
})
export class SlideDataDialogComponent implements OnInit {

  @Input() public dhivehiEnabled = true;
  slide_data: slideData = new slideData(null, new Language('Dhv'), null);
  onAdd = new EventEmitter();

  constructor(
    public dialogRef: MatDialogRef<SlideDataDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: slideData
    ) { }

  ngOnInit() {
    this.slide_data = this.data;
  }

  closeDialog(confirmation: boolean) {
    this.dialogRef.close(confirmation);
  }

  changeLanguage() {
    if (this.dhivehiEnabled) {
      this.slide_data.language.language_code = 'Dhv';
    } else {
      this.slide_data.language.language_code = 'Eng';
    }
  }

  addData() {
    // this.store.dispatch(new adminActions.CreateUser(this.user));
    this.onAdd.emit(this.slide_data);
    this.dialogRef.close(true);
  }

}
