
import { Action, createFeatureSelector, createSelector } from '@ngrx/store';

import * as ControlActions from './control.actions';
import { ControlState } from '../../store/app.state';





export const initialState: ControlState = {
    // news: [],
    // isLoaded: false,
    // // pageState: {currentPage: null, perPage: null, lastPages: null, totalResults: null},
    // lists: [],
    isLoaded: true,
    cgSettings: null,
    newsCgState: null
  };

export type Actions = ControlActions.All;
export interface ActionWithPayload<T> extends Action {
  payload: T;
}

export function reducer(state = initialState, action: ActionWithPayload<any>): ControlState {
  console.log(state, action);

  switch (action.type) {

    case ControlActions.GET_NEWS_CG_NEWS: {
      return {
        ...state,
        isLoaded: false
      };
    }

    case ControlActions.GET_NEWS_CG_NEWS_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        newsCgState: action.payload
      };
    }

    case ControlActions.GET_CG_SETTINGS: {
      return {
        ...state,
        isLoaded: false
      };
    }

    case ControlActions.GET_CG_SETTINGS_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        cgSettings: action.payload
      };
    }

    case ControlActions.LOGOUT_CONTROL: {
      return initialState;
    }

    default: {
      return state;
    }
  }

}

export const getReviewNewsState = createFeatureSelector<ControlState>('ControlState');
