import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpErrorResponse } from '@angular/common/http';

import { environment } from './../../../environments/environment';
import { News, ListOrder } from '../../news/models/news';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ControlService {
  private apiUrl = environment.apiUrl;

  constructor(
    private http: HttpClient
  ) { }

  // get selected list
  getNewsCgNews () {
    return this.http.get<any>(this.apiUrl + 'control/news/cg');
  }

  // get selected list
  getNewsCgSettings () {
    console.log('Getting cg settings')
    //return this.http.get<any>(this.apiUrl + 'control/cg/settings/all');
    return this.http.get<any>(this.apiUrl + 'control/news/cg');
  }

  //update news cg text
  updateNewsCgText(news_bar_id: number) {
    return this.http.put<any>(this.apiUrl + 'control/news/cg/update/', { "news_bar_id": news_bar_id, "ip": '' });
  }

  stopNewsCg() {
    return this.http.post<any>(this.apiUrl + 'control/cg/news/stop', {});
  }


}
