import { Action } from '@ngrx/store';
import { News, ListOrder, NewsList } from '../../news/models/news';

export const LOGOUT_CONTROL = '[Control] LOGOUT_CONTROL';

export const GET_NEWS_CG_NEWS = '[Control] GET_NEWS_CG_NEWS';
export const GET_NEWS_CG_NEWS_SUCCESS = '[Control] GET_NEWS_CG_NEWS_SUCCESS';
export const GET_NEWS_CG_NEWS_ERROR = '[Control] GET_NEWS_CG_NEWS_ERROR';

export const UPDATE_NEWS_CG_NEWS = '[Control] UPDATE_NEWS_CG_NEWS';
export const UPDATE_NEWS_CG_NEWS_SUCCESS = '[Control] UPDATE_NEWS_CG_NEWS_SUCCESS';
export const UPDATE_NEWS_CG_NEWS_ERROR = '[Control] UPDATE_NEWS_CG_NEWS_ERROR';

export const GET_CG_SETTINGS = '[Control] GET_CG_SETTINGS';
export const GET_CG_SETTINGS_SUCCESS = '[Control] GET_CG_SETTINGS_SUCCESS';
export const GET_CG_SETTINGS_ERROR = '[Control] GET_CG_SETTINGS_ERROR';

export const STOP_NEWS_CG = '[Control] STOP_NEWS_CG';
export const STOP_NEWS_CG_SUCCESS = '[Control] STOP_NEWS_CG_SUCCESS';
export const STOP_NEWS_CG_ERROR = '[Control] STOP_NEWS_CG_ERROR';

export class LogoutEditorState implements Action {
  readonly type = LOGOUT_CONTROL;
}


/**
 * --------------------------------------------------> Get cg news
 */
export class GetNewsCgNews implements Action {
  readonly type = GET_NEWS_CG_NEWS;
}

export class GetNewsCgNewsSuccess implements Action {
  readonly type = GET_NEWS_CG_NEWS_SUCCESS;
  constructor(public payload: NewsList) {}
}

export class GetNewsCgNewsError implements Action {
  readonly type = GET_NEWS_CG_NEWS_ERROR;
}


/**
 * --------------------------------------------------> Get cg Settings
 */
export class GetCgSettings implements Action {
  readonly type = GET_CG_SETTINGS;
}

export class GetCgSettingsSuccess implements Action {
  readonly type = GET_CG_SETTINGS_SUCCESS;
  constructor(public payload: NewsList) {}
}

export class GetCgSettingsError implements Action {
  readonly type = GET_CG_SETTINGS_ERROR;
}


/**
 * --------------------------------------------------> Update cg news
 */
export class UpdateNewsCgNews implements Action {
  readonly type = UPDATE_NEWS_CG_NEWS;
  constructor(public payload: { news_bar_id: number }) {}
}

export class UpdateNewsCgNewsSuccess implements Action {
  readonly type = UPDATE_NEWS_CG_NEWS_SUCCESS;
}

export class UpdateNewsCgNewsError implements Action {
  readonly type = UPDATE_NEWS_CG_NEWS_ERROR;
}


/**
 * --------------------------------------------------> stop cg
 */
export class StopNewsCg implements Action {
  readonly type = STOP_NEWS_CG;
  //constructor(public payload: { news_bar_id: number }) {}
}

export class StopNewsCgSuccess implements Action {
  readonly type = STOP_NEWS_CG_SUCCESS;
}

export class StopNewsCgError implements Action {
  readonly type = STOP_NEWS_CG_ERROR;
}



export type All = any;
