
import {catchError, mergeMap,  map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Observable ,  of } from 'rxjs';
import { Action } from '@ngrx/store';




import {Actions, Effect, ofType} from '@ngrx/effects';

import * as ControlActions from './control.actions';
import { ControlService } from '../services/control.service';

@Injectable()
export class ControlEffects {

  constructor(
    private actions$: Actions,
    private controlService: ControlService,
  ) { }

  @Effect()
  GetNewsCgNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<ControlActions.GetNewsCgNews>(ControlActions.GET_NEWS_CG_NEWS),
      mergeMap(action =>
        this.controlService.getNewsCgNews().pipe(
        map((data) => {
          console.log(data);
          return new ControlActions.GetNewsCgNewsSuccess(data);
        }),
        catchError(() => of(new ControlActions.GetNewsCgNewsError())),)
      )
    );



  /*GetCgSettings$: Observable<Action> = this.actions$.
    ofType<ControlActions.GetNewsCgNews>(ControlActions.GET_NEWS_CG_NEWS)
    .mergeMap(action =>
      this.controlService.getNewsCgSettings()
      .map((data) => {
        console.log(data);
        return new ControlActions.GetCgSettingsSuccess(data);
      })
      .catch(() => of(new ControlActions.GetCgSettingsError()))
    );*/


  @Effect()
  UpdateNewsCgNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<ControlActions.UpdateNewsCgNews>(ControlActions.UPDATE_NEWS_CG_NEWS),
      mergeMap(action =>
        this.controlService.updateNewsCgText(action.payload.news_bar_id).pipe(
        map((data) => {
          console.log(data);
          return new ControlActions.UpdateNewsCgNewsSuccess();
        }),
        catchError(() => of(new ControlActions.UpdateNewsCgNewsError())),)
      )
    );


    @Effect()
    StopNewsCg$: Observable<Action> = this.actions$.
      pipe(
        ofType<ControlActions.UpdateNewsCgNews>(ControlActions.STOP_NEWS_CG),
        mergeMap(action =>
          this.controlService.stopNewsCg().pipe(
          map((data) => {
            console.log(data);
            return new ControlActions.StopNewsCgSuccess();
          }),
          catchError(() => of(new ControlActions.StopNewsCgError())),)
        )
      );
}
