import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSidenav } from '@angular/material/sidenav';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ControlService } from '../services/control.service';
import { OnairService } from '../services/onair.service';
import { SlideDataDialogComponent } from '../slide-data-dialog/slide-data-dialog.component';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { Language } from '../../news/models/news';

@Component({
  selector: 'app-onair',
  templateUrl: './onair.component.html',
  styleUrls: ['./onair.component.css']
})
export class OnairComponent implements OnInit {

  sidePanelOpened = true;
  sidePanelOpened_noNews = true;

  slides: Slide[];
  selectedSlide: Slide;
  settings_selected_slide: number;
  destroySubject$: Subject<void> = new Subject();

  @ViewChild('sidebar', {static: true}) sidenav: MatSidenav;

  constructor(
    private onAirService: OnairService,
    public dialog: MatDialog,
    public snackBar: MatSnackBar
  ) { }

  ngOnInit() {
    this.onAirService.getSlides().subscribe(onair => {
      this.slides = onair;
      console.log(this.slides);
    });
  }



  sideBarToggle() {
    this.sidenav.toggle();
  }

  isOver(): boolean {
    return window.matchMedia(`(max-width: 960px)`).matches;
  }

  onSelectSlide(slide:Slide){
    this.selectedSlide = slide;
    this.settings_selected_slide = slide.id;
    console.log(slide)
  }

  addSlideData(slideId: number) {
    let content = new slideData(null, new Language('Dhv'), this.settings_selected_slide, 0);

    let dialogRef = this.dialog.open(SlideDataDialogComponent, { width: '40%', height: 'auto', maxHeight: '95%', data: content });

    dialogRef.componentInstance.onAdd.subscribe((result: slideData) => {
      console.log(result)
      this.onAirService.addSlideData(result).subscribe(re => {
        console.log(re)
        this.slides = re;
        this.slides.forEach(data => {
          if(data.id === this.settings_selected_slide) {
            this.selectedSlide = data;
          }
        })
      })
    },
    err=>{
      console.log("status code--->"+err.status);
    });
  }

  deleteContent(id: number) {
    this.onAirService.deleteSlideData(id).subscribe(re => {

      this.onAirService.getSlides().subscribe(onair => {
        this.slides = onair;

        this.slides.forEach(data => {
          if(data.id === this.settings_selected_slide) {
            this.selectedSlide = data;
          }
        })
      });
    },
    err=>{
      console.log("Err Deleting Slide Data, status code--->"+err.status);
    });
  }


}




export class Slide {
  id: number;
  slide_id: string;
  slide_name: string;
  onairslidecontent: slideData[];
}

export class slideData{
  data: string;
  language: Language;
  on_air_slides_id: number;
  active: number;

  constructor(data?: string, lang?: Language, s_id?: number, active?: number) {
    this.data     = data;
    this.language = lang;
    this.on_air_slides_id = s_id;
    this.active   = active;
  }
}
